$appBackground: #f3f3f3;
$containerBackground: #ffffff;
$upgradesBackground: #cacaca;
$tooltipBackground: #e2e2e2;
$workersBackground: #fff2da;
$workerBackground: #f3f3f3;
$mineBlockBackground: #763d17;
$boughtUpgradeColor: #ffd702;
$notificationColor: #293239;

$containerBorders: 2px solid #515151;
$workersBorders: 3px solid #cbcbcb;

$mainFont: Arial, sans-serif;

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.app {
  position: relative;
  height: 100vh;
  max-width: 1024px;
  background: $appBackground;
  margin: 0 auto;
}

.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background: $containerBackground;
}

.left-panel {
  width: 45%;
  height: 100%;
  display: flex;
  flex-direction: column;
  border: $containerBorders;
}

.title {
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: $mainFont;
  font-size: 22px;
  border-bottom: $containerBorders;
}

.mine-block {
  width: 100%;
  height: 500px;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  background: $mineBlockBackground;
}

.mine {
  width: 300px;
  height: 300px;
  background-image: url("./resources/images/mine.jpg");
  background-size: cover;
  background-position: center;
  border-radius: 100%;
  cursor: pointer;
  transition: all .1s ease-out;

  &:hover {
    transform: scale(1.1);
  }
}

.money-block {
  width: 100%;
  height: 40px;
  font: 16px $mainFont;
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: pre;
}

.right-panel {
  width: 45%;
  height: 100%;
  display: flex;
  flex-direction: column;
  border: $containerBorders;
}

.upgrades {
  width: 100%;
  display: flex;
  flex-direction: column;
  background: $upgradesBackground;

  &-title {
    height: 30px;
    width: 100%;
    font: 18px $mainFont;
    padding: 3px;
  }

  &-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: 200px;
    width: 100%;
    border-top: $containerBorders;
    border-bottom: $containerBorders;
  }
}

.upgrade {
  width: 50px;
  height: 50px;
  background-size: cover;
  background-position: center;
  position: relative;
  border: 2px dashed transparent;
  cursor: pointer;

  &--bought {
    border-color: $boughtUpgradeColor;
  }

  .tooltip {
    position: absolute;
    width: 200px;
    border: $containerBorders;
    display: none;
    flex-direction: column;
    background: $tooltipBackground;
    top: 110%;
    left: 110%;

    &-title {
      font: bold 20px $mainFont;
      min-height: 20px;
      width: 100%;
    }

    &-price {
      font: 18px $mainFont;
      min-height: 20px;
      width: 100%;
    }

    &-description {
      font: 16px $mainFont;
      width: 100%;
    }
  }

  &:hover {
    .tooltip {
      display: flex;
    }
  }
}

.workers {
  width: 100%;
  display: flex;
  flex-direction: column;
  background: $workersBackground;

  &-title {
    height: 30px;
    width: 100%;
    font: 18px $mainFont;
    padding: 3px;
  }

  &-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    border-top: $containerBorders;
    border-bottom: $containerBorders;
  }
}

.worker {
  display: flex;
  margin: 6px auto;
  width: 100%;
  height: 60px;
  align-items: center;
  justify-content: space-around;
  padding: 3px;
  cursor: pointer;
  border: $workersBorders;
  background: $workerBackground;

  &-image {
    height: 45px;
    width: 45px;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
  }

  &-name {
    font: 20px $mainFont;
  }

  &-price {
    font: 16px $mainFont;
  }

  &-quantity {
    font: 22px $mainFont;
  }
}

.inc-text {
  position: absolute;
  pointer-events: none;
  font: 22px $mainFont;
  color: white;
  opacity: 1;
  transition: all 3s;

  &--flying-up {
    transform: translate(0, -100px);
    font-size: 10px;
    opacity: 0;
  }
}

.dust-particle {
  position: absolute;
  border-radius: 100%;
  width: 100px;
  height: 100px;
  opacity: 0.6;
  pointer-events: none;
  background-size: cover;
  background-position: center;
}

.notifications-container {
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 300px;
  padding: 10px;
  bottom: 5%;
  left: 5%;
  background: rgba(255,255,255, 0.2);
  border-radius: 10px;
  pointer-events: none;
}

.notification {
  display: block;
  width: 100%;
  padding: 3px;
  font: 14px $mainFont;
  color: $notificationColor;
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                   supported by Chrome, Edge, Opera and Firefox */
}
